.App {
  text-align: center;
}

.EightPuzzle {
  margin: 50px auto;
  padding: 10px 10px;
  width: 50%;
  max-width: 350px;
  min-width: 300px;
  height: 300px;
  background-color: whitesmoke;
  display: flex;
  flex-wrap: wrap;
}

.NumberPlate {
  width: 30%;
  height: 90px;
  background-color: skyblue;
  border-radius: 5px;
  margin-left: 10px;
  font-size: 50px;
  color: white;
}
  .NumberPlate.selected {
    background-color: orangered;
  }
  .NumberPlate.selectable {
    background-color: orange;
  }
  .NumberPlate.completed {
    background-color: yellow;
    pointer-events: none;
  }

.TetrisBox-row {
  display: inline-block;
  background-color: gray;
  width: 20px;
  height: 20px;
  border: thin solid black;
}
#col_3 {
  border-bottom: thin solid black;
}

  .TetrisBox-row.a {
    background-color: orange;
  }
  .TetrisBox-row.b {
    background-color: red;
  }
  .TetrisBox-row.c {
    background-color: lightgreen;
  }
  .TetrisBox-row.d {
    background-color: blue;
  }
  .TetrisBox-row.e {
    background-color: skyblue;
  }
  .TetrisBox-row.f {
    background-color: cyan;
  }
  .TetrisBox-row.g {
    background-color: gold;
  }
  .TetrisBox-row.fix {
    background-color: purple;
  }

.StartGame {
  font-weight: bold;
  font-size: 20px;
  cursor: pointer;
}

.CompMessage {
  font-weight: bold;
  font-size: 30px;
}

.tryAgainButton {
  cursor: pointer;
  font-weight: bold;
}
  .tryAgainButton:hover {
    opacity: 0.8;
  }
